.dashboardContainer {
  height: 100%;
  flex: 0 0 330px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.controlPanel {
  padding: 20px;
  border-bottom: 1px solid #cdd2d6;
  display: flex;
  align-items: center;
  button {
    border-radius: 10px;
    color: #fff;
  }
  .search {
    margin-left: auto;
    color: #000;
    border-radius: 10px;
  }
}

.chatNameList {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;
}

.chatName {
  cursor: pointer;
  .icon {
    margin-right: 10px;
    img {
      object-fit: contain;
      width: 35px;
    }
  }
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 56px;
  width: 100%;
  padding: 0 22px;

  .name {
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    width: calc(100% - 25px);
    color: #67737d;
  }
}

.settings {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  border-top: 1px solid #cdd2d6;
  height: 70px;
  margin-top: auto;
  padding-left: 5px;
}
.settingsIcon {
  margin-left: 10px !important;
  border-radius: 6px !important;
  padding: 5px !important;
  svg {
    font-size: 28px;
  }
  .iconName {
    font-size: 14px;
    color: #67737d;
    margin-left: 10px;
  }
}
