.dashboardContainer {
  height: 100%;
  flex: 0 0 330px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding: 10px 30px;
}

.componentTitle {
  font-family: monospace;
}

.component {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  margin-bottom: 30px;
  .name {
    font-weight: 600;
    padding-left: 10px;
  }
  .sourceIcon {
    border: 1px solid #cdd2d6;
    padding: 2px;

    img {
      object-fit: scale-down;
      width: 30px;
    }
  }
}

.source {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  margin-bottom: 15px;
}

